.navbar {
    display: flex;
    align-items: center;
    height: 60px;
    width: 100%;
    background-color: white;
    border-bottom: 1px solid #dbdbdb;
    justify-content: center;
}

.navbar__barContent {
    /*width: 100%;*/
    /*height:60px;*/
    border-bottom: 1px solid #dbdbdb;
    background-color: white;
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    align-items: stretch;
    box-sizing: border-box;
    justify-content: center;
    max-width: 975px;
    padding: 0 20px;
    width: 100%;
    height: 60px;
}

.logo-containers {
    flex: 1 0 332px;
}

.logo-container {
    display: flex;
    flex: 0 0 auto;
    justify-content: flex-start;
    flex-direction: row;
    align-items: stretch;
    align-content: stretch;
    box-sizing: border-box;
    position: relative;
}

.navbar_logo{
    margin-top: 10px;
    width: 103px;
}
.img-control {
    margin-top: 10px;
}
.Search-container {
    width: 500px;
}

.image-11{
    display: flex;
}

.modal-container{
    display: flex;
    margin-left: 20px;
    margin-top: 19px;
    width: 30%;
    margin-right: 5px;
}

