.main {
    position: relative;
    margin: 0;
    background: #FAFAFA;
}

.main-box {
    display: flex;
    width: 821px;
    margin: 0 auto;
    height: 100%;
    flex-direction: row;
    flex-grow: 1;
    flex-shrink: 0;
    font: inherit;
    font-size: 100%;
    position: relative;
    vertical-align: baseline;
}

.left {
    float: left;
    margin-right: 32px;
    max-width: 470px;
    width: 100%;
}

.suggest {
    margin-top: 31px;
}

@media (max-width: 1000px) {
    .main {
        width: 470px;
        margin: 0 auto;
    }
}